	module.exports = {"data":{"markdownRemark":{"html":"<p>For my August GDS Challenge, I decided that I wanted to tackle the K-12 environment. At this point, most of your K-12 stuff is created is marketed and sold by vendors. But I can easily envision an environment where teachers themselves or perhaps an in-district LX group created interactions helped to create performance-centered interactions to help students practice performance on any number of subjects. There might even be a place for the students themselves to create their own interactions using the rapid development tools we have available (gasp!). To that end, I wanted to recreate one of my favorite social studies games growing up: Where in the World is Carmen Sandiego?</p>\n<h2>Why in the World Carmen Sandiego?</h2>\n<p>I choose Carmen for a couple of reasons. Firstly it’s what I would call a serious game. It embeds the need (and motivation) to look up real-world historical, cultural, and geographical information (originally using encyclopedias) in order to solve in-game problems (namely, to follow the V.I.L.E agents and arrest them). That creates a great environment for learning. Throughout the cases, you also begin to internalize certain facts through repetition. <a href=\"/blog/throwback-thursday-3-design-lessons-learned-from-where-in-the-world-is-carmen-sandiego/\">As I wrote in my breakdown of the game</a>, by the end of a few cases, you know in which country the thin-spined porcupine or the Taureg people reside. Secondly, it allows me to revisit game development from a slightly different angle. This go round, I’d like to incorporate somewhere between 3 to 5 cases and allow users to rank up over time.</p>\n<h2>In Progress</h2>\n<p>My vision for this one was pretty straight forward. I created a tiny lookbook of Carmen Sandiego games both past and present and created a look and feel based on them. I will say though that, partly owing to not having all the graphics I need yet, I went ahead and started prototyping in PowerPoint which has proved very helpful. As I’ve mentioned before, for me, PowerPoint works well because it’s a medium in which I feel free to delete something if it doesn’t work. It’s also allowed me to take some time and really flesh out my ideas about some of the game elements. Looking to make this a reality by the end of the month!</p>\n<figure>\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Suspect page prototype\" title=\"\" src=\"/static/Slide8-a1ddae241258d3ea8a2db8f67805825c-1626e.png\" srcset=\"/static/Slide8-a1ddae241258d3ea8a2db8f67805825c-076ec.png 203w,\n/static/Slide8-a1ddae241258d3ea8a2db8f67805825c-7ae34.png 405w,\n/static/Slide8-a1ddae241258d3ea8a2db8f67805825c-1626e.png 810w,\n/static/Slide8-a1ddae241258d3ea8a2db8f67805825c-9af38.png 1215w,\n/static/Slide8-a1ddae241258d3ea8a2db8f67805825c-91e19.png 1280w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n</figure>\n<figure>\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Speaking with a victim prototype\" title=\"\" src=\"/static/Slide11-e6e56784d853834aed33d9b661369057-1626e.png\" srcset=\"/static/Slide11-e6e56784d853834aed33d9b661369057-076ec.png 203w,\n/static/Slide11-e6e56784d853834aed33d9b661369057-7ae34.png 405w,\n/static/Slide11-e6e56784d853834aed33d9b661369057-1626e.png 810w,\n/static/Slide11-e6e56784d853834aed33d9b661369057-9af38.png 1215w,\n/static/Slide11-e6e56784d853834aed33d9b661369057-91e19.png 1280w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n</figure>\n<figure>\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Getting a case prototype\" title=\"\" src=\"/static/Slide3-f6b51a889de78c0ab71547de0a21896b-1626e.png\" srcset=\"/static/Slide3-f6b51a889de78c0ab71547de0a21896b-076ec.png 203w,\n/static/Slide3-f6b51a889de78c0ab71547de0a21896b-7ae34.png 405w,\n/static/Slide3-f6b51a889de78c0ab71547de0a21896b-1626e.png 810w,\n/static/Slide3-f6b51a889de78c0ab71547de0a21896b-9af38.png 1215w,\n/static/Slide3-f6b51a889de78c0ab71547de0a21896b-91e19.png 1280w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n</figure>\n<figure>\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Robbery start scene prototype\" title=\"\" src=\"/static/Slide5-6f3be8e38fc00833c69228062527c4a2-1626e.png\" srcset=\"/static/Slide5-6f3be8e38fc00833c69228062527c4a2-076ec.png 203w,\n/static/Slide5-6f3be8e38fc00833c69228062527c4a2-7ae34.png 405w,\n/static/Slide5-6f3be8e38fc00833c69228062527c4a2-1626e.png 810w,\n/static/Slide5-6f3be8e38fc00833c69228062527c4a2-9af38.png 1215w,\n/static/Slide5-6f3be8e38fc00833c69228062527c4a2-91e19.png 1280w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n</figure>","frontmatter":{"date":"August 13, 2015","path":"/blog/august-go-design-something-challenge-carmen-sandiego-like","title":"August Go Design Something Challenge: Carmen Sandiego-Like","tags":["carmen sandiego","development","game design","K-12","social studies"],"categories":["Design","GDS Challenge","Showing my Work"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.5422396856581533,"src":"/static/Carmen-Sandiego-Like-696eb5ccca81f9236f69f6c31751a294-8be45.png","srcSet":"/static/Carmen-Sandiego-Like-696eb5ccca81f9236f69f6c31751a294-28994.png 203w,\n/static/Carmen-Sandiego-Like-696eb5ccca81f9236f69f6c31751a294-f0efd.png 405w,\n/static/Carmen-Sandiego-Like-696eb5ccca81f9236f69f6c31751a294-8be45.png 785w","sizes":"(max-width: 785px) 100vw, 785px"}}}}}},"pathContext":{}}

